.filterSortBar{
    display:flex;
    position:fixed;
    bottom:0%;
    left:0%;
    background:#fff;
    justify-content: space-evenly;
    z-index:9999;
    align-items: center;
    padding:1rem;
    font-weight:600;
}

.filterSortBar .mainBtn:active, .filterSortBar .mainBtn:hover{
    background: #212529;
}

.offcanvas .card.active{
    background: #212529;
    color:#fff;
}

.sortOffCanvas .offcanvas-header{
    justify-content: space-between;
}

.sortOffCanvas {
    bottom:7%;
    height:auto !important;
}

.nav-pills .nav-link.active, .nav-pills .show>.nav-link {
    background:#000;
}

.nav-link {
    color:#000;
}

@media screen and (max-width:920px){
    .form-check{
        margin-bottom:10px;
    }
    .form-check input, .form-check label{
        font-size:16px;
    }
  }