.productCard .addtoCart{
    background:#000;
    color:#fff;
    border-radius:5px;
}

.productCard .outOfStock{
    background:#000;
    color:#fff;
    border-radius:5px;
    opacity: 0.3;
    cursor: default;
}

.productCard .cardTitle{
    font-weight: 500;
    white-space: nowrap;
    text-overflow: ellipsis;
    font-family: 'Futrst' !important;
    overflow: hidden;
}

.productCard .discountedPrice{
    font-weight:500;
    font-size:18px;
    white-space: nowrap;
}

.productCard:hover{
    text-decoration: none;
}

.productCard .originalPrice{
    color: rgba(0,0,0,0.4);
    text-decoration: line-through;
    margin-right:5px;
    font-size:12px;
}

.productCard .productImage{
    width:100%;
    height:100%;
    border-radius:5px;
}

.productCard .seperator{
    width: 1px;
    background: rgb(0, 0, 0);
    height: 100%;
    margin: 5px;
}

.productCard .discount{
    font-size:10px;
    color:green;
    font-weight: 500;
}

.productCard .attributeValue{
    font-size:14px;
}

.productCard .cardImage{
    height:100%;
    width:100%;
}

.productCard .ratingCount{
    font-weight:500;
    font-size:14px;
}

.productCard{
    text-decoration: none;
    color:#000;
}

.notifyBanner{
    position:absolute;
    left:0;
}

@media screen and (max-width:520px){
    .productsDetails{
        min-height:290px;
        position:relative;
    }

   .productsDetails .addtoCart{
        position:absolute;
        left:5%;
        bottom:5%;
        width:90%;
    }
    .productsDetails .outOfStock{
        position:absolute;
        left:5%;
        bottom:5%;
        width:90%;
        opacity: 0.3;
        cursor: default;
    }
}