.productsCard{
    margin-bottom:10px;
}

.filterButton{
    background:#000;
    color:#fff;
}

.filterButton:not(.collapsed) {
background:#fff;
color: #000;
}

.productsCard .productCard img{
    height:230px;
}

.productsFilters .applyFilterBtn{
    padding: 30px 47px;
}

.productsFilters .applyFilterBtn button{
    color: white;
    background: black;
    height: 40px;
    border-radius: 8px;
    width: 119px;
}

.accordion-button {
    height:50px;
}

@media screen and (max-width:720px){
    .productsCard .productCard img{
        height:165px;
    }

    .productsCard{
        width:50%;
    }
}